import { Card, CardContent, CardHeader, CardTitle, CardDescription } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Badge } from "@/components/ui/badge"
import { CheckCircle2, Circle, Clock, MessageSquare, ArrowRight } from "lucide-react"

const messages = [
  { sender: "Alice Cooper", preview: "Is Court 3 available for 2 hours?", time: "5m ago", unread: true },
  { sender: "Bob Wilson", preview: "Thanks for the great service!", time: "1h ago", unread: false },
  { sender: "Charlie Brown", preview: "I need to cancel my booking for tomorrow.", time: "3h ago", unread: false },
]

const tasks = [
  { text: "Confirm booking request #2024", done: false, priority: "High" },
  { text: "Check net on Court 2", done: false, priority: "Medium" },
  { text: "Restock drinks in fridge", done: true, priority: "Low" },
]

export function ActionItems() {
  return (
    <div className="grid gap-4 md:grid-cols-2">
      {/* Messages */}
      <Card className="border-none shadow-sm h-full">
        <CardHeader className="pb-3">
          <div className="flex items-center justify-between">
            <CardTitle className="text-base font-semibold">Recent Messages</CardTitle>
            <Button variant="ghost" size="sm" className="h-8 text-xs">View all</Button>
          </div>
          <CardDescription>3 new messages today</CardDescription>
        </CardHeader>
        <CardContent>
          <div className="space-y-4">
            {messages.map((msg, i) => (
              <div key={i} className="flex items-start gap-3 group cursor-pointer">
                 <div className="relative">
                    <div className="h-9 w-9 rounded-full bg-muted flex items-center justify-center">
                        <MessageSquare className="h-4 w-4 text-muted-foreground" />
                    </div>
                     {msg.unread && (
                        <span className="absolute -top-1 -right-1 h-3 w-3 rounded-full bg-primary border-2 border-background" />
                     )}
                 </div>
                 <div className="flex-1 space-y-1">
                    <div className="flex items-center justify-between">
                        <p className="text-sm font-medium leading-none">{msg.sender}</p>
                        <span className="text-xs text-muted-foreground">{msg.time}</span>
                    </div>
                    <p className="text-xs text-muted-foreground line-clamp-1">{msg.preview}</p>
                 </div>
                 <Button variant="ghost" size="icon" className="h-8 w-8 opacity-0 group-hover:opacity-100 transition-opacity">
                    <ArrowRight className="h-4 w-4" />
                 </Button>
              </div>
            ))}
          </div>
        </CardContent>
      </Card>

      {/* Tasks */}
      <Card className="border-none shadow-sm h-full">
        <CardHeader className="pb-3">
          <div className="flex items-center justify-between">
            <CardTitle className="text-base font-semibold">Tasks</CardTitle>
             <Button variant="ghost" size="sm" className="h-8 text-xs">View all</Button>
          </div>
          <CardDescription>2 pending tasks</CardDescription>
        </CardHeader>
        <CardContent>
          <div className="space-y-3">
             {tasks.map((task, i) => (
                 <div key={i} className="flex items-start gap-3 group">
                    <button className="mt-0.5 text-muted-foreground hover:text-primary transition-colors">
                        {task.done ? (
                            <CheckCircle2 className="h-5 w-5 text-emerald-500" />
                        ) : (
                            <Circle className="h-5 w-5" />
                        )}
                    </button>
                    <div className="flex-1">
                        <p className={`text-sm ${task.done ? 'line-through text-muted-foreground' : 'font-medium'}`}>
                            {task.text}
                        </p>
                    </div>
                    {task.priority === "High" && !task.done && (
                        <Badge variant="destructive" className="text-[10px] h-5 px-1.5">High</Badge>
                    )}
                 </div>
             ))}
          </div>
        </CardContent>
      </Card>
    </div>
  )
}
